$index: calc(1vw + 1vh);

.calendar-part {
   height: 100%;
   width: 65%;
   display: flex;
   flex-flow: column;
   align-items: center;
   justify-content: space-between;

   @media screen and (max-width: 1024px) {
      width: 95%;
   }
   @media screen and (orientation: portrait) {
      width: 95%;
   }

   .calendar-part-wrapper {
      display: flex;
      flex-flow: column;
      margin: auto 0;
      padding: $index;
      backdrop-filter: blur(10px) brightness(.9);
      box-shadow: 0 0 4px rgba(#111, .7);
      border-radius: $index;
      background: transparent;
      transition: .2s ease;

      &:hover {background: rgba(#111, .5);}
   }
}