$index: calc(1vw + 1vh);

.submit-btn {
   width: min-content;
   background: rgba(#111, .5);
   box-shadow: 0 0 4px 1px #03A9F4;
   border-radius: calc($index);
   font-size: calc($index / 1.4);
   padding: calc($index / 1.5) calc($index * 2);
   transition: .2s ease;
   font-weight: 700;
   text-transform: uppercase;
   white-space: nowrap;

   &:hover {
      background: rgba(#fff, 1);
      box-shadow: 0 0 4px 4px #03A9F4;
   }
}