$index: calc(1vw + 1vh);

.calendar-days-of-week {
   display: grid;
   grid-template: 1fr / repeat(7, 14%);
   text-align: center;
   font-size: calc($index / 1.35);
   text-transform: uppercase;
   margin: 0 0 calc($index / 2) 0;

   li {text-shadow: calc($index / -2) calc($index / 2) calc($index / 5) #111;}
}