$index: calc(1vw + 1vh);

.show-remind {
   height: 100%;   
   padding: 0 0 $index;

   .show-remind-wrapper {
      display: flex;
      flex-flow: column;
      width: 100%;
      height: 100%;
      margin: 0 auto;
      position: relative;

      .title-time-container {
         display: flex;
         justify-content: space-between;
         align-items: center;

         input {
            width: 60%;
            font-size: calc($index / 1.2);
            font-weight: 700;

            @media screen and (max-width: 1199px) {
               font-size: calc($index * 1.15);
            }
         }

         .create-remind-time-container {
            display: flex;
            align-items: center;
            position: relative;
            font-size: $index;

            .time-input {
               width: calc($index * 2);
               font-size: calc($index / 1.2);
               font-weight: 700;
               background: transparent;
               color: #fff;
               padding: calc($index / 3) 0;
               text-align: center;

               @media screen and (max-width: 1199px) {
                  font-size: calc($index * 1.2);
               }
               
               &:focus {
                  outline: none;
                  color: #fff;
               }
            }

            @media screen and (max-width: 1199px) {
               span {font-size: calc($index * 1.1);}
            }
         }
      }
      
      .btn-wrapper {
         display: flex;
         align-items: center;
         justify-content: space-between;

         .button-repeat {
            min-width: calc($index * 7.36);
            font-size: calc($index / 1.4);
            color: #fff;
            border-top: 2px solid transparent;
            border-bottom: 2px solid #fff;
            border-left: 2px solid transparent;
            border-right: 2px solid transparent;
            padding: calc(var(--index) / 3) calc(var(--index) * 1.25);
            transition: .3s;

            &:hover {
               border-top: 2px solid #fff;
               border-bottom: 2px solid #fff;
               border-left: 2px solid #fff;
               border-right: 2px solid #fff;
               border-radius: $index;
               background: rgba(#111, .5);
            }

            @media screen and (max-width: 1199px) {
               font-size: $index;
            }
         }
      }
   }
}

.btn-show-hide-remind {
   position: relative;
   width: calc($index * 1.25);
   height: calc($index * 1.25);

   .arrow-polygon {
      position: absolute;
      top: 0;
      left: 0;
      clip-path: polygon(100% 50%, 50% 100%, 0 50%, 0 30%, 50% 80%, 100% 30%);
      pointer-events: none;
      width: 100%;
      height: 100%;
      display: block;
      background: #00c9a7;
      transition: .1s;

      @media screen and (max-width: 1199px) {
         top: -10%;
      }
   }

   &.active {
      .arrow-polygon {
         top: 30%;
         transform: rotate(180deg);
      }
   }

   &:hover {
      .arrow-polygon {
         background: #fff;
      }
   }

   @media screen and (max-width: 1199px) {
      width: calc($index * 1.5);
      height: calc($index * 1.5);
   }
}