$index: calc(1vw + 1vh);
:root {
   --remindHeight: 0;
   --remindMoveX: 0;
}

.remind-part {
   height: 100%;
   width: 35%;
   margin: 0 0 0 $index;
   position: relative;

   @media screen and (max-width: 1024px) {
      width: 5%;
   }
   @media screen and (orientation: portrait) {
      width: 5%;
   }
}
.container-remind-part {
   padding: $index 0 $index $index;
   backdrop-filter: blur(10px) brightness(.9);
   box-shadow: 0 0 4px rgba(#111, .7);
   border-radius: $index 0 0 $index;
   position: absolute;
   width: 100%;
   height: 100%;
   right: 0;
   transition: .2s ease;
   background: transparent;
   // overflow: hidden;

   &.active {background: rgba(#111, .8);}

   .remind-part-show-hide {
      touch-action: none;
      width: calc($index * 2.5);
      border-radius: 0 $index $index 0;
      position: absolute;
      height: 100%;
      left: 0;
      top: 0;
      background: #00493b;
      display: none;
      z-index: 3;
      overflow: hidden;
      flex-flow: column;
      justify-content: space-around;

      .arrow-svg {
         position: relative;
         display: block;
         width: calc($index * 3);
         height: calc($index * 3);
         left: calc($index / -4);
         transition: .3s ease-out;
      }

   }

   &:hover {background: rgba(#111, .5);}

   .btn-create-remind {
      bottom: $index;
      right: $index;
      position: fixed;
      border-radius: 0;
      padding: calc($index / 2);
      box-shadow: 0 0 0 2px transparent;
      transition: .3s;
      overflow: hidden;
   
      &::before {
         position: absolute;
         content: '';
         display: block;
         width: calc(var(--index) * 3);
         height: 100%;
         background: linear-gradient(65deg, transparent 40%, rgba(255, 255, 255, .8) 50%, transparent 60%);
         left: -100%;
         top: 0;
         transition: .25s ease-out;
      }
   
      .btn-create-remind-polygon {
         clip-path: polygon(0 45%, 45% 45%, 45% 0, 55% 1%, 55% 45%, 100% 45%, 100% 55%, 55% 55%, 55% 99%, 45% 100%, 45% 55%, 0 55%);
         background: #00c9a7;
         width: calc($index * 1.5);
         height: calc($index * 1.5);
         pointer-events: none;

         @media screen and (max-width: 1200px) {
            width: calc($index * 2.5);
            height: calc($index * 2.5);
         }
      }
   
      &:hover {
         box-shadow: 0 0 0 2px #00c9a7;
         border-radius: calc($index / 2);
   
         &:before {
            position: absolute;
            content: '';
            display: block;
            width: calc(var(--index) * 4);
            height: 100%;
            background: linear-gradient(65deg, transparent 40%, rgba(255, 255, 255, .8) 50%, transparent 60%);
            left: 100%;
            top: 0;
         }
      }
   }

   @media screen and (max-width: 1024px) {
      padding: $index 0 $index calc($index * 3.5);
      width: calc(100% * 8);
      left: var(--remindMoveX);
      transition: 50ms linear;
      background: transparent;

      .wrapper-scroll-reminds {pointer-events: none;}

      &::before {
         content: '';
         position: fixed;
         display: block;
         width: 300vw;
         height: 200vh;
         top: 0;
         left: -300%;
         opacity: 0;
         background: rgba(#111, .9);
         z-index: 0;
         pointer-events: none;
         transition: .3s;
      }

      .remind-part-show-hide {
         display: flex;
      }

      &:not(:hover) {background: rgba(#111, .5);}

      &.active {
         z-index: 3;

         .wrapper-scroll-reminds {pointer-events: fill;}
        
         &::before {opacity: 1;}

         .arrow-svg {
            transform: rotate(180deg);
         }
      }
   }

   @media screen and (orientation: portrait) {
      padding: $index 0 $index calc($index * 3.5);
      width: calc(100% * 15);
      left: var(--remindMoveX);
      transition: 50ms linear;
      background: transparent;

      .wrapper-scroll-reminds {pointer-events: none;}

      &::before {
         content: '';
         position: fixed;
         display: block;
         width: 300vw;
         height: 200vh;
         top: 0;
         left: -300%;
         opacity: 0;
         background: rgba(#111, .9);
         z-index: 0;
         pointer-events: none;
         transition: .3s;
      }

      .remind-part-show-hide {
         display: flex;
      }

      &:not(:hover) {background: rgba(#111, .5);}

      &.active {
         z-index: 3;

         .wrapper-scroll-reminds {pointer-events: fill;}
        
         &::before {opacity: 1;}

         .arrow-svg {
            transform: rotate(180deg);
         }
      }

      .remind-part-show-hide {
         display: flex;
      }
   }
}

.wrapper-scroll-reminds {
   height: calc(100vh - ($index * 3));
   width: calc(100% - (($index / 2) - $index + 8px));
   position: relative;
   overflow-y: scroll;
   padding: calc($index * 2) 0;

   .show-hide-container-remind {
      margin: $index 0 $index auto;
      padding: 0 $index 0 calc($index / 2);
      max-height: 25%;
      max-height: calc($index * 4);
      overflow: hidden;
      transition: .35s ease;
      position: relative;

      &::before {
         content: '';
         position: absolute;
         width: calc(100% - $index);
         height: 3px;
         background: rgba(#fff, .3);
         bottom: 0;
         left: 0;
         transition: .3s ease;
      }

      &.active {
         max-height: var(--remindHeight);
         &::before {
            background: rgba(#fff, 1);
         }
      }
   }
}

.wrapper-scroll-reminds::-webkit-scrollbar-track {background: transparent;}
.wrapper-scroll-reminds::-webkit-scrollbar {width: 4px;}
.wrapper-scroll-reminds::-webkit-scrollbar-thumb {background: #00c9a7;}