$index: calc(1vw + 1vh);

.main-wrapper {
   width: 100%;
   height: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
   height: calc(100vh - ($index * 3));
   overflow: hidden;

   @media screen and (max-width: 1024px) {
      justify-content: space-between;
   }
}