$index: calc(1vw + 1vh);
$windowW: 100vw;
$windowH: 100vh;

.calendar-item {
   width: min-content;
   margin: calc($index / 2) $index;
   animation: calendar-rerender .2s ease-in forwards;
   box-shadow: calc($index / -2) calc($index / 2) calc($index / 2) calc($index / 4) rgba(#111, .8);
   border-radius: calc($index / 1.3);

   @media screen and (max-width: 1600px) {
      margin: calc($index / 1.5) $index;
   }

   @media screen and (max-width: 1200px) {
      border-radius: $index;
   }
   @media screen and (max-width: 1200px) and (orientation: portrait) {
      border-radius: 0;
      margin: calc($index / 1.5) calc($index / 2);
   }

   @media screen and (max-width: 1024px) {
      margin: calc($index / 2) calc($index * 1.5);
   }
   @media screen and (max-width: 1024px) and (orientation: portrait) {
      margin: calc($index / 1.5);
   }

   @keyframes calendar-rerender {
      0%{opacity: 0;}
      100%{opacity: 1;}
   }

   .calendar-item-btn {
      width: calc($index * 2.3);
      height: calc($index * 2.3);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: calc($index / 1.3);

      @media screen and (max-width: 1600px) {
         width: calc($index * 2.5);
         height: calc($index * 2.5);
      }

      @media screen and (max-width: 1200px) {
         border-radius: $index;
         width: calc($index * 3);
         height: calc($index * 3);
      }
      // @media screen and (max-width: 1200px) and (orientation: portrait) {
      //    width: calc($index * 2.8);
      //    height: calc($index * 4);
      //    border-radius: 0;
      // }

      @media screen and (max-width: 1024px) {
         width: calc($index * 3.5);
         height: calc($index * 3.5);
      }
      @media screen and (max-width: 1200px) and (orientation: portrait) {
         width: calc(1vw * 8);
         height: calc(1vw * 12);
         border-radius: 0;
      }

      span {
         color: #fff;
         pointer-events: none;
         font-size: $index;
         font-weight: 700;
         transition: .3s ease;

         &.remind {
            position: relative;
            display: flex;
            justify-content: center;
            &::before {
               content: '';
               display: block;
               position: absolute;
               background: #F7DC6F;
               width: calc($index / 4);
               height: calc($index / 4);
               bottom: calc($index / -4);
               border-radius: 50%;
            }

            @media screen and (max-width: 1200px) {
               &::before {
                  width: calc($index / 3);
                  height: calc($index / 3);
                  bottom: calc($index / -3);
               }
            }

            @media screen and (max-width: 1024px) {
               &::before {
                  width: calc($index / 2.5);
                  height: calc($index / 2.5);
                  bottom: calc($index / -2.5);
               }
            }
         }

         @media screen and (max-width: 1200px) {
            font-size: calc($index * 1.1);
         }
         @media screen and (max-width: 1200px) and (orientation: portrait) {
            font-size: calc($index * 1.5);
         }
         @media screen and (max-width: 1024px) {
            font-size: calc($index * 1.5);
         }
      }
      .calendar-span-another {color: rgba(#fff, .2);}

      &.button-another {
         background: rgba(#111, .7);
         box-shadow: 0 0 0 3px rgba(#fff, .1);
         transition: .3s ease;
      }
      &.button-current {
         box-shadow: 0 0 0 3px #fff;
         background: rgba(#00c9a7, 1);
         transition: .3s ease;

         @media screen and (min-width: 1200px) {
            &:hover {
               background: #fff;
               box-shadow: 0 0 6px 8px #00c9a7;
   
               span {color: #00c9a7;}
            }
         }

         // &:hover {
         //    background: #fff;
         //    box-shadow: 0 0 6px 8px #00c9a7;

         //    span {color: #00c9a7;}
         // }

         &.today {
            background: #fff;
            box-shadow: 0 0 10px 3px #00c9a7, 0 0 0 3px #00c9a7;
            span {color: #00c9a7;}
         }

         &.active {
            background: #fff;
            box-shadow: 0 0 6px 8px #00c9a7;
            span {color: #00c9a7;}
         }
         
         @media screen and (max-width: 1200px) and (orientation: portrait) {
            box-shadow: 0 0 0 4px #fff;
         }
      }
   }
}