$index: calc(1vw + 1vh);

.input.input-password.recover {
   width: 80%;
   padding: calc($index / 2) $index;
}

.auth-form.recover {
   width: calc(var(--index) * 25);
   .buttons {
      text-align: center;
      h2 {
         line-height: 1.5;
         padding: 0 0 calc($index * 2) 0;
         border-bottom: 2px solid #00c9a7;
      }
   }

}