$index: calc(1vw + 1vh);

.buttons-date {
   width: $index;
   height: 100%;
   position: absolute;

   &.btn-prev {left: 0;}
   &.btn-next {right: 0;}

   .button-navigate {
      pointer-events: none;
      position: absolute;
      width: inherit;
      height: inherit;
      top: 0;
      width: 150%;

      &.btn-prev {
         clip-path: polygon(40% 0, 15% 50%, 40% 100%, 25% 100%, 0% 50%, 25% 0%);
         background: #fff;
         left: 0;
         transition: .2s ease-in;
      }
      &.btn-next {
         clip-path: polygon(65% 0%, 90% 50%, 65% 100%, 50% 100%, 75% 50%, 50% 0);
         background: #fff;
         right: 0;
         transition: .2s ease-in;
      }
   }

   &:active {
      .button-navigate {
         transition: 0s;

         // &.btn-prev {background: #03A9F4;}
         &.btn-prev {background: #00c9a7;}
         // &.btn-next {background: #03A9F4;}
         &.btn-next {background: #00c9a7;}
      }
   }

   &:hover {
      .button-navigate {
         
         &.btn-prev {left: calc($index / -3);}
         &.btn-next {right: calc($index / -3);}
      }
   }

   
   @media screen and (max-width: 1199px) {
      width: calc($index * 1.25);
   }
}