$index: calc(1vw + 1vh);

textarea, input {
   font-size: calc($index / 1.3);
   background: transparent;
   border: none;
   box-shadow: 0 0 2px #fff;
   border-radius: $index;
   padding: calc($index / 3) calc($index / 1.5);
   margin: $index 0;

   @media screen and (max-width: 1200px) {
      font-size: calc($index * 1);
   }
}

input {
   border-radius: 0;
   box-shadow: none;
   border-bottom: 2px solid #00c9a7;

   &::placeholder {
      color: #00c9a7;
      transition: .3s;
}
   &:focus {
      outline: none;
      border-bottom: 2px solid #fff;
      color: #fff;

      &::placeholder {color: #fff;}
   }
}

textarea:focus {
   outline: none;
   box-shadow: 0 0 2px 4px #00c9a7;
}
span {
   font-size: $index;
   font-weight: 700;
   height: min-content;
}

.create-remind, .show-remind {
   opacity: 0;
   animation: container-animation .25s ease-out forwards;
   
   @keyframes container-animation {
      0%{
         opacity: 0;
      }
      100%{
         opacity: 1;
      }
   }
}

.button-repeat {
   min-width: calc($index * 8.7);
   font-size: calc($index / 1.3);
   color: #fff;
   border-top: 2px solid transparent;
   border-bottom: 2px solid #fff;
   border-left: 2px solid transparent;
   border-right: 2px solid transparent;
   padding: calc(var(--index) / 3) calc(var(--index) * 1.5);
   transition: .3s;

   &:hover {
      border-top: 2px solid #fff;
      border-bottom: 2px solid #fff;
      border-left: 2px solid #fff;
      border-right: 2px solid #fff;
      border-radius: $index;
      background: rgba(#111, .5);
   }

   @media screen and (max-width: 1200px) {
      font-size: $index;
      min-width: calc($index * 7);
      padding: calc(var(--index) / 3) 0;
   }
}

.suggest-create-remind {
   width: 100%;
   height: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   z-index: 1;
   position: relative;

   h3 {
      font-size: calc($index * 1.5);
      font-weight: 300;
   }
   h3, .btn-create {margin: $index 0;}
}