$index: calc(1vw + 1vh);

.current-date {
   position: relative;
   display: flex;
   align-items: center;

   span {
      font-size: $index;
      text-shadow: calc($index / -2) calc($index / 2) calc($index / 5) #111;
   }

   .buttons-menu {
      position: absolute;
      width: calc(100% + ($index * 2));
      height: 100%;
      left: calc($index * -1);
   }

   .current-year-wrapper {
      margin: 0 calc($index * 2);
      position: relative;
   }

   .current-month-wrapper {
      margin: 0 calc($index * 2);
      min-width: calc($index * 5.5);
      text-align: center;
      position: relative;
   }

   .current-day-wrapper {
      text-align: center;
      display: flex;
      justify-content: space-between;

      span {
         &:nth-child(1){min-width: calc($index * 1.25);}
         &:nth-child(2){margin: 0 $index;}
         &:nth-child(3){
            min-width: calc($index * 6);
            text-align: left;
         }
      }
   }

   @media screen and (max-width: 1199px) {
      span {
         font-size: calc($index * 1.25);
      }

      .current-day-wrapper {
         span {
            &:nth-child(3){
               min-width: calc($index * 8.5);
            }
         }
      }
   }
}