$index: calc(1vw + 1vh);

.create-remind {
   height: 100%;   
   padding: 0 0 $index;

   .btn-close-create-remind {
      width: calc($index * 1.25);
      height: calc($index * 1.25);
      position: relative;
      right: calc(-100% + calc($index * 3));
      top: $index;
      z-index: 1;

      .close-polygon {
         clip-path: polygon(15% 0, 0 15%, 35% 50%, 0 85%, 15% 100%, 50% 65%, 85% 100%, 100% 85%, 65% 50%, 100% 15%, 85% 0, 50% 35%);
         background: #F44336;
         width: 100%;
         height: 100%;
         display: block;
         transition: .15s;
      }

      &:hover {
         .close-polygon {
            background: #fff;
         }
      }

      @media screen and (max-width: 1199px) {
         width: calc($index * 1.5);
         height: calc($index * 1.5);
      }
   }

   .create-remind-wrapper {
      display: flex;
      flex-flow: column;
      width: 100%;
      height: 100%;
      margin: 0 auto;
      position: relative;
      padding: $index $index 0;

      .title-time-container {
         display: flex;
         justify-content: space-between;
         align-items: center;

         input {
            width: 60%;
            font-size: calc($index / 1.2);
            font-weight: 700;

            @media screen and (max-width: 1199px) {
               font-size: calc($index * 1.15);
            }
         }

         .create-remind-time-container {
            display: flex;
            align-items: center;
            position: relative;

            .time-input {
               width: calc($index * 2);
               font-size: calc($index / 1.2);
               font-weight: 700;
               background: transparent;
               color: #fff;
               padding: calc($index / 3) 0;
               text-align: center;

               @media screen and (max-width: 1199px) {
                  font-size: calc($index * 1.15);
               }
               
               &:focus {
                  outline: none;
                  color: #fff;
               }
            }
         }
      }

      .cyclicity-createBtn-wrapper {
         display: flex;
         justify-content: space-between;
         align-items: center;
      }
      
      .btn-wrapper {
         display: flex;
         align-items: center;
         justify-content: space-between;

      }
   }
}