$index: calc(1vw + 1vh);

.wrapper-form {
   // background-image: linear-gradient(45deg, #215f00, #215f00, #B0DAB9, #EFEFBB, #DAD299, #D3959B, #f2709c, #ff9472, #faaca8, #ddd6f3, #D4D3DD, #EFEFBB);
   // background-image: linear-gradient(45deg,  #649173, #327440, #274046, #327440, #649173, #649173, #327440, #274046, #274046, #327440, #649173, #B0DAB9);
   // background: linear-gradient(45deg, #f2709c 0%, #ff9472 0%, #ddd6f3 90%, #faaca8 100%);
   // background: linear-gradient(45deg, #215f00 0, #215f00 25%, #ddd6f3 25%, #ddd6f3 50%, #274046 50%, #274046 75%, #E6DADA 75%, #E6DADA 90%, #E6DADA 90%, #215f00 100%);
   // background-image: linear-gradient(45deg, #215f00 0, #215f00 10%, #B0DAB9 10%, #B0DAB9 20%, #EFEFBB 20%, #EFEFBB 30%, #DAD299 30%, #DAD299 40%, #D3959B 40%, #D3959B 50%, #f2709c 50%, #f2709c 60%, #ff9472 60%, #ff9472 70%, #faaca8 70%, #faaca8 80%, #ddd6f3 80%, #ddd6f3 90%, #D4D3DD 90%, #D4D3DD 100%, #EFEFBB 100%);
   // background: linear-gradient(45deg, #0B5345 0%, #0B5345 25%, #117A65 25%, #117A65 50%, #16A085 50%, #16A085 75%, #1ABC9C 75%, #1ABC9C 100%);
   // background-size: 300% 300%;
   // // background-size: 200% 200%;
   // animation: anim-background 40s ease infinite;

   // @keyframes anim-background {
   //    0%{background-position: 0 50%;}
   //    50%{background-position: 100% 50%;}
   //    100%{background-position: 0 50%;}
   // }

   height: calc(100vh - calc($index * 3));
   display: flex;
   align-items: center;
   justify-content: center;
   
   .auth-form {
      border-radius: $index;
      backdrop-filter: blur(10px) brightness(.6);
      height: min-content;
      position: relative;

      input {
         width: 100%;
      }

      .buttons {
         display: flex;
         align-items: center;
         justify-content: space-between;
         flex-direction: column;
         width: 100%;
         padding: 0 $index;

         .button-redirect {
            font-size: calc($index / 1.4);
            position: relative;
            overflow: hidden;
            white-space: nowrap;
            color: #fff;
            transition: .3s;

            &::before {
               content: '';
               display: block;
               position: absolute;
               bottom: 0;
               left: -100%;
               background: #00c9a7;
               width: 100%;
               height: 3px;
               transition: .3s ease;
            }

            &:hover {
               color: #00c9a7;
               &::before {
                  left: 0;
               }
            }

            @media screen and (max-width: 1024px) {
               font-size: calc($index);
            }
         }
      }

      .navigation-buttons {
         width: min-content;
         text-align: center;
         button:nth-child(1) {
            margin: 0 0 $index;
            white-space: nowrap;
         }
      }
   }
}