$index: calc(1vh + 1vw);

.input {
   box-shadow: 0 0 4px 1px #00c9a7;
   border-bottom: none !important;
   border-radius: calc($index / 2);
   font-size: calc($index / 1.3);
   padding: calc($index / 2) 0 calc($index / 2) calc($index * 2);
   margin: calc($index) auto;

   @media screen and (max-width: 1024px) {
      font-size: calc($index);
   }
}