$index: calc(1vw + 1vh);

.auth-form {
   h2 {
      margin-bottom: $index;
      font-size: $index;

      @media screen and (max-width: 1024px) {
         font-size: calc($index * 1.5);
      }
   }

   .span-message {top: calc($index * 4);}
   .input.input-email.code {
      width: 30%;
      padding: calc($index / 2) $index;
   }
}

.button-redirect-wrapper {
   position: relative;
   text-align: center;

   .svg-arrow {
      width: $index;
      height: $index;
      position: absolute;
      pointer-events: none;
      right: calc($index * -5);
      top: 0;
      opacity: 0;
      transition: .2s ease-out;

      svg {
         left: 0;
         top: 0;
         width: 100%;
         height: 90%;
      }
   }

   &:hover {
      .svg-arrow {
         right: calc($index * -2);
         opacity: 1;
      }
   }
}